<template>
    <div class="home">
      <home-header></home-header>
      <home-swiper :swiperList="swiperList"></home-swiper>
      <home-nav :iconList="iconList"></home-nav>
      <home-favourite :favouriteList="recommendList"></home-favourite>
      <home-weekend :weekendList="weekendList"></home-weekend>
    </div>
</template>

<script>
  import HomeHeader from './base/Header'
  import HomeSwiper from './base/Swiper'
  import HomeNav from './base/Nav'
  import HomeFavourite from './base/Favourite'
  import HomeWeekend from './base/Weekend'

  export default {
        name: "Hmoe",
        components:{
          HomeHeader,
          HomeSwiper,
          HomeNav,
          HomeFavourite,
          HomeWeekend
        },
        data(){
          return {
            iconList:[],
            recommendList:[],
            swiperList:[],
            weekendList:[]
          }
        },
        created(){
          //this.getData()
        },
        methods:{
           getData(){
            // let {iconList,recommendList,swiperList,weekendList} = await getHome();
            // this.iconList = iconList;
            // this.recommendList = recommendList;
            // this.swiperList = swiperList;
            // this.weekendList = weekendList;
             this.$axios.post('/qunaer').then(res =>{
               let {iconList,recommendList,swiperList,weekendList} = JSON.parse(res).data;
               this.iconList = iconList;
               this.recommendList = recommendList;
               this.swiperList = swiperList;
               this.weekendList = weekendList;
            })
          }

        },
        activated() {
          this.getData()
          document.documentElement.scrollTop = 0

        }
  }
</script>

<style scoped>
  .home{
    background-color: #fff;
  }
</style>



