<template>
    <div class="alphabet">
      <ul>
        <li v-for="(city,key) in cities" :key="city.id" @click="handleClick(key)">{{key}}</li>
      </ul>
    </div>
</template>

<script>
    export default {
        name: "Alphabet",
        props:["cities"],
        methods:{
          handleClick(letter){
            this.$store.commit("changLetter",letter)
          }
        }
    }
</script>

<style lang="stylus" coped>
  .alphabet
    position:fixed
    display:flex
    flex-direction:column
    justify-content:center
    right:0
    top:1.6rem
    bottom:0
    width:.4rem
    li
      line-height: .5rem
      text-align: center
      color:#e08d28
      font-weight:600
</style>



