<template>
    <div>
      <div class="comment">
        <h3 class="border-bottom">用户评论</h3>
        <div v-for="(item,index) in commentInfo" :key="index" class="border-bottom info">
          <div class="comment-desc">{{item.desc}}</div>
          <div class="comment-img" @click="handleChang(item)">
            <ul class="clearfix">
              <li v-for="(img,index) in item.imgs" :key="index" v-if="index < 6">
                <img :src="img" alt="">
              </li>
            </ul>
            <p class='banner-number' v-show="item.imgs.length > 6">
              <i class="iconfont icon-tupian"></i>
              <span>{{item.imgs.length}}</span>
            </p>
          </div>
          <common-transition>
            <common-seeing :gallaryImgs="item.imgs" v-show="item.isShow" @close="handleClose(item)"></common-seeing>
          </common-transition>
        </div>
      </div>

    </div>
</template>

<script>
  import CommonSeeing from '@/components/common/Seeimg'
  import CommonTransition from '@/components/common/Transition'

  export default {
        name: "Comment",
        props:["commentInfo"],
        components:{
          CommonSeeing,
          CommonTransition
        },
        data(){
          return{
          }
        },
        methods:{
          handleChang(item){
            item.isShow = !item.isShow
          },
          handleClose(item){
            item.isShow = !item.isShow
          }
        }
    }
</script>

<style lang="stylus" scoped>
  .border-bottom
    &::before
      border-color:#999
  .comment
    padding: .2rem
    .info
      margin: .2rem 0
      padding-bottom: .2rem
    h3
      position: relative;
      padding-left:.4rem
      line-height: 1rem;
      font-size: 0.32rem;
    h3::after
      content: " ";
      position: absolute;
      top: .38rem;
      left: .2rem;
      width: .06rem;
      height: .25rem;
      background: #1ba9ba;
      border-radius: .04rem;
    .comment-desc
      word-break: break-all;
      word-wrap: break-word;
      line-height: .42rem;
      font-size: .26rem;
      color: #616161;
    .comment-img
      position relative
      li
        float: left;
        width: 33.33%;
        margin-bottom: .1rem
        img
          width :100%
      .banner-number
        position:absolute
        z-index:10
        right:0
        bottom:.2rem
        height: .36rem
        padding: 0 .2rem
        line-height: .36rem
        border-radius: .2rem
        background-color :rgba(255,255,255,.8)
        font-size: .24rem



</style>



