<template>
    <div class="search">
      <input
        type="text"
        class='header-input'
        placeholder='请输入城市名和拼音'
        v-model="keyword">
      <div v-show="keyword" class="search-content">
        <ul>
          <li class="search-item border-bottom" v-for="(item,index) in filterCities" :key="index">{{item.name}}</li>
        </ul>
      </div>
    </div>
</template>

<script>
    export default {
        name: "Search",
        props:["cities"],
        data(){
          return {
            keyword : ''
          }
        },
        computed:{
          filterCities(){
            const result = [];
            for(let key in this.cities){
              this.cities[key].forEach((v,i) =>{
                if(v.name.includes(this.keyword ) || v.spell.includes(this.keyword )){
                  result.push(v)
                }
              })
            }
            return result
          }
        }
    }
</script>

<style lang="stylus" scoped>
  .border-bottom::before
    border-color:#aaa
  .search
    position:relative
    height:.72rem
    padding:0.1rem
    background-color:#00bcd4
    .header-input
      width:100%
      height:100%
      border-radius:.1rem
      text-align:center
    .search-content
      position:fixed
      z-index:20
      overflow:hidden
      top:1.78rem
      left:0
      right:0
      bottom:0
      background-color:#ccc
      .search-item
        line-height:.8rem
        padding-left:.2rem
        background-color:#fff
</style>



