<template>
    <div class="weekend">
      <h3 class="title">周末去哪儿</h3>
      <router-link
        class="week-list"
        v-for="(item,index) in weekendList"
        :key="index"  tag="div"
        :to="{path:'/week',query:{id:item.id,title:item.title}}">
        <img :src="item.imgUrl" alt="" class="week-ing">
        <p>{{item.title}}</p>
        <p class="week-desc">{{item.desc}}</p>
      </router-link>
    </div>
</template>

<script>
    export default {
        name: "HomeWeekend",
        props:["weekendList"]
    }
</script>

<style lang="stylus" scoped>
  .title
    padding: 0.25rem
    background-color #f1f1f1
  .week-list
    padding-bottom:0.25rem
    .week-ing
      width 100%
    p
      padding: 0.25rem  0.25rem 0
    .week-desc
      color: #888
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 1;
      overflow: hidden;
</style>



