<template>
  <div class="list" ref="wrapper">
    <div>
      <div class="list-current">
        <h3 class='list-title border-topbottom' >当前城市</h3>
        <div class="city">
          <span>{{$store.state.city}}</span>
        </div>
      </div>

      <div class="list-hot">
        <h3 class='list-title border-topbottom'>热门城市</h3>
        <div class="city">
          <span v-for="(item,index) in hotCities" :key="index" @click="handleClick(item.name)">{{item.name}}</span>
        </div>
      </div>

      <div class="list-item" v-for="(city,key) in cities">
        <h3 class='list-title border-topbottom' :ref="key">{{key}}</h3>
        <ul>
          <li class='city-item border-bottom' v-for="(item,index) in  city" :key="index" @click="handleClick(item.name)">{{item.name}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
    import Vuex from 'vuex'
    import BScroll from 'better-scroll'
    export default {
        name: "CityList",
        props:["hotCities","cities"],
        methods:{
          handleClick(name){
            this.$store.commit("changCity",name)
            this.$router.push("/")
          }
        },
        computed:{
          ...Vuex.mapState(['letter'])//得到仓库里的letter值
        },
        mounted() {
          this.scroll =  new BScroll(this.$refs.wrapper,{
            click : true ///使用滚轮组件想在里面使用点击事件必须加上 click：true
          });

       },
        watch:{
          letter(value){
            this.scroll.scrollToElement(this.$refs[value][0])
          }
        }
    }
</script>

<style lang="stylus" scoped>
  .border-bottom:before
    border-color:#aaa
  .list
    position:fixed
    overflow: hidden
    top: 1.78rem
    left: 0
    right: 0
    bottom: 0
  .list-title
    line-height: 0.54rem
    background: #eee
    padding-left: 0.2rem
    color: #666
    font-size: 0.26rem
  .city
    padding-left:.15rem
    span
      display:inline-block
      width:33%
      padding:.3rem .3rem
      /*margin:.1rem*/
      border-right:.01rem solid #ccc
      border-bottom:.01rem solid #ccc
      text-align:center
      box-sizing:border-box
      border-radius:.05rem
  .city-item
    line-height:.8rem
    padding-left:.2rem
</style>



