<template>
    <div class="headr">
        <i class="iconfont icon-fanhui back"></i>
        <div class="headr-search">
          <i class="iconfont icon-sousuo "></i>
          <input type="text" placeholder="输入城市/景点/游玩主题">
        </div>
      <router-link class="headr-right" tag="div" to="/city">
        <span>{{$store.state.city}}</span>
        <i class="iconfont icon-jiantou"></i>
      </router-link>
    </div>
</template>

<script>
    import Vuex from 'vuex'
    export default {
        name: "HomeHeader",
    }
</script>

<style lang="stylus" scoped>
  .headr
    display: flex;
    width 100%
    height 0.88rem
    background-color :#00BCD4
    line-height 0.88rem
    color: #fff
    text-align: center;
    .back
      padding: 0 0.2rem
      font-weight bold
    .headr-search
      flex: 1
      height: .6rem;
      line-height: .6rem;
      background-color #ffffff
      margin: .14rem 0;
      border-radius: .06rem;
      text-align:left
      .icon-sousuo
        color: #ccc
        vertical-align: middle
        padding-left: 0.2rem
      input
        width :80%
        font-size : 0.2rem
    .headr-right
      margin: 0 0.2rem
</style>



