<template>
    <div class="detail">
      <detail-header></detail-header>
      <detail-banner
       :bannerImg="bannerImg"
        :gallaryImgs="gallaryImgs"
        :sightName="sightName">
      </detail-banner>
      <detail-card :cardInfo="cardInfo"></detail-card>
      <detail-recommend :recommendInfo="recommendInfo" >
        <h3 class="border-bottom">去哪儿推荐</h3>
      </detail-recommend>
      <detail-calendar :calendarInfo="calendarInfo"></detail-calendar>
      <detail-comment :commentInfo="commentInfo"></detail-comment>
    </div>
</template>

<script>
    import DetailHeader from './base/Header'
    import DetailBanner from './base/Banner'
    import DetailCard from './base/Card'
    import DetailCalendar from './base/Calendar'
    import DetailRecommend from './base/Recommend'
    import DetailComment from './base/Comment'

    export default {
        name: "Dateil",
        components:{
          DetailHeader,
          DetailBanner,
          DetailCard,
          DetailCalendar,
          DetailRecommend,
          DetailComment
        },
        data(){
          return {
            bannerImg: '',
            gallaryImgs: [],
            sightName : '',
            cardInfo : {},
            recommendInfo :{},
            calendarInfo :{},
            commentInfo:{}
          }
        },
        created(){
         // this.getDate()
        },
        methods:{
           getDate(){
            this.$axios.post('/details/'+this.$route.params.id).then(res =>{
              let {bannerImg,gallaryImgs,sightName,cardInfo,recommendInfo,calendarInfo,commentInfo}= JSON.parse(res).data;
              this.bannerImg = bannerImg;
              this.gallaryImgs = gallaryImgs;
              this.sightName = sightName;
              this.cardInfo = cardInfo;
              this.recommendInfo =recommendInfo;
              this.calendarInfo = calendarInfo;
              this.commentInfo = commentInfo;
            })
          }

        },
        activated() {
          this.getDate()
          document.documentElement.scrollTop = 0;
        }
    }
</script>

<style scoped>
    .detail{
      background: #ffffff;
    }
</style>



