<template>

 <div class="nav">
   <swiper :options="swiperOption" >
     <!-- slides -->
     <swiper-slide v-for="(imgs,index) in filterIngs" :key="index">
       <div class="list">
         <div class="item" v-for="(item,index) in imgs" :ke="index">
           <div><img class='item-img' :src="item.imgUrl" alt=""></div>
           <span class="item-desc">{{item.desc}}</span>
         </div>
       </div>
     </swiper-slide>

     <!-- Optional controls -->
     <div class="swiper-pagination"  slot="pagination"></div>
   </swiper>
 </div>

</template>

<script>
  import { swiper, swiperSlide } from 'vue-awesome-swiper'
  export default {
        name: "HomeNav",
        props:["iconList"],
        data() {
          return {
            swiperOption: {
              pagination: {
                el: '.swiper-pagination',
              },
            }
          }
        },
      computed:{
        filterIngs(){
          let imgs = [];
          this.iconList.forEach((item,index) =>{
           let page = ~~(index/8);
           if(!imgs[page]){
             imgs[page] = [];
           }
            imgs[page].push(item)
          })
          return imgs
        }
      }
    }
</script>

<style lang="stylus" scoped>
  .nav >>> .swiper-pagination .swiper-pagination-bullet
    width : 0.12rem
    height : 0.12rem
  .nav >>> .swiper-pagination
    bottom: .1rem
  .nav >>> .swiper-container
    padding-bottom: .4rem
  .list
    display flex
    flex-wrap:wrap
    .item
      width: 25%;
      margin-top:0.2rem
      text-align center
      img
        width: 1.1rem;
        height: 1.1rem;
</style>



