<template>
    <div class="week" >
      <div class="header">
        <router-link tag="i" to="/"  class="iconfont  icon-fanhui header-back"></router-link>
        <span>{{$route.query.title}}</span>
      </div>
      <div ref="wrapper" class="list">
        <ul>
          <li v-for="(item,index) in weekInfo" :key="index">
            <img :src="item.imgUrl" alt="" class="week-ing">
            <p class="week-title">{{item.title}}</p>
            <p class="week-desc">{{item.desc}}</p>
            <span class="price">￥<span class="price-number">{{item.price }}</span>起</span>
          </li>
        </ul>
      </div>

    </div>
</template>

<script>
  import BScroll from 'better-scroll'
    export default {
        name: "Week",
        created(){
         // this.getData()
        },
        data(){
          return {
            weekInfo : {}
          }
        },
        filters:{
          // star(value){
          //   return "￥"+ value+"起"
          // }
        },
        methods:{
           getData(){
            this.$axios.post('/week?id='+this.$route.query.id).then(res =>{
              let {weekInfo} = JSON.parse(res).data;
              this.weekInfo = weekInfo;
            })
          },

        },
        activated() {
          this.getData()
          document.documentElement.scrollTop = 0
        },
        mounted(){
          this.scroll = new BScroll(this.$refs.wrapper,{
            click: true
          })
        }

    }
</script>

<style lang="stylus" scoped>
  .week
    .header
      position:relative
      background:#00bcd4
      line-height:.86rem
      color:#fff
      text-align:center
      .header-back
        position:absolute
        left:0
        top:0
        width:.8rem
        color:#fff
      h2
        text-align:center
        font-size:.4rem
    .list
      position:fixed
      overflow: hidden
      top: .86rem
      left: 0
      right: 0
      bottom: 0
    ul
      li
        position: relative;
        border-bottom: .2rem solid #ddd;
        .week-ing
          width 100%
        .week-title
          font-size:.35rem
        .week-desc
          padding-bottom :0.3rem
          color: #666
        p
          padding-top: .2rem
          padding-left: .2rem
          background-color #fff
        .price
          position:absolute
          right:0.26rem
          bottom:0.7rem
          color #ff9800
          .price-number
            font-size: .4rem

</style>



