<template>
   <div>
     <transition name="xk">
      <slot></slot>
     </transition>
   </div>
</template>

<script>
    export default {
        name: "Transtion",

    }
</script>

<style lang="stylus" scoped>
  /*.v-enter,.v-leave-to*/
    /*opacity: 0*/
  /*.v-enter-active,.v-leave-active*/
    /*transition: opacity 1s*/
  .xk-enter,.xk-leave-to
    opacity: 0
  .xk-enter-active,.xk-leave-active
    transition: opacity .5s


</style>



