<template>
    <div class="city">
      <city-header></city-header>
      <city-search :cities="cities"></city-search>
      <city-list :cities="cities" :hotCities="hotCities"></city-list>
      <city-alphabet :cities="cities"></city-alphabet>
    </div>
</template>

<script>
  import CityHeader from './base/Header'
  import CitySearch from './base/Search'
  import CityList from './base/List'
  import CityAlphabet from './base/Alphabet'


  export default {
        name: "City",
        components:{
          CityHeader,
          CitySearch,
          CityList,
          CityAlphabet
        },
        data(){
          return {
            cities : [],
            hotCities:[]
          }
        },
        created(){

        },
        methods:{
           getData() {
            this.$axios.post('/city').then(res => {
              let {cities, hotCities} = JSON.parse(res).data;
              this.cities = cities;
              this.hotCities = hotCities;
            })
          }
        },
        activated() {
          this.getData()
        }
  }
</script>

<style scoped>

</style>



